import { Progress } from 'iview';
import {
  mapComponent
} from 'deps/utils';
import wrap from './wrap';

Progress.name = 'Progress';
let NewCtor = wrap(Progress); // 加垫片
// 加垫片注册
NewCtor = mapComponent({
  Ctor: NewCtor,
  libName: 'iview'
});

export default NewCtor;



